@import url(https://fonts.googleapis.com/css?family=Titillium+Web);
@import url(https://fonts.googleapis.com/css?family=Titillium+Web);
html{
  min-height:100%;
}

body {
  font-family: 'Titillium Web';
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100%;
  margin: 0;
  background: linear-gradient(150deg, #522546 0%, #311d3f 100%);
  background-image: linear-gradient(150deg, #522546 0%, #311d3f 100%);
}

h1{
  font-weight:bold;
  color:#b45d7a;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
}

h2, h3 {
  color:#b45d7a;
}
p, h4 {
  color: #EFD2DC;
}

h5{
  color: #EFD2DC;
  margin-bottom:0px;
}

p.smalltext{
  font-size:small;
  margin: 0px;
}

p.contactinfo{
  display: inline-block;
  transition: color 0.5s;
}
p.contactinfo:hover{
  color:#b45d7a;
}
p.jumbocontent{
  color: #EFD2DC;
}

.card{
  background-color: #311d3f !important;
  color: #311d3f;
  fill: #311d3f;
}

.jumbotron{
  background-color: #311d3f !important;
  width: 85%;
  margin: auto;
}

.btn-flat {
  background-color: #EFD2DC !important;
  margin-top: 0.5em;
}

.btn-flat2 {
  background-color: #EFD2DC !important;
  margin-top: 0.5em;
}

.card-flat3{
  color: red
}

hr {
  color: white;
  background-color:white;
  width:50%
}

a {
  width:2em;
  height:2em;
}

img.contacts{
  display: inline-block;
  width: 2em;
  height: 2em;
  margin: 1em;
}

img.skills{
  display: inline-block;
  width: 5em;
  height: 5em;
  margin: 1em;
  padding: 1em; 
  border-radius: 2em;
  border: 3px solid #b45d7a;
  background-color: #EFD2DC;
  transition: background-color 0.5s;
}

img.skills:hover{
  background-color: #b45d7a;
}

img.skills2 , img.skills3{
  display: inline-block;
  width: 7em;
  height: 7m;
  margin: 1em;
  padding: 1em; 
  border-radius: 1em;
  border: 3px solid #cfb9ba;
  background-color: #EFD2DC;
  transition: color 0.5s;
  transition: background-color 0.5s;
}
img.skills3:hover{
  background-color: #b45d7a;
}
div.content {
  padding-top: 3em;
  font-family: 'Titillium Web';
  padding-left: 2em;
  padding-right: 2em;
  text-align: center;
}
div.specialcolumn{
  width: 54em;
  margin: auto;
}
a.gitbutton{
  padding: 1em; 
  border-radius: 2em;
  border: 3px solid #b45d7a;
  background-color: #EFD2DC;
  margin-right: 0.5em;
  transition: background-color 0.5s;
}

a.gitbutton:hover{
  background-color: #b45d7a;
}




html,
body {
  margin: 0;
}

#App {
  font-family: 'Titillium Web';
  height: 100vh;
}

#page-wrap {
  text-align: center;
  overflow: auto;
  top: 45%;
  position: relative;
}

.bm-item {
  display: inline-block;
  /* Our sidebar item styling */
  font-family: 'Titillium Web';
  margin-bottom: 10px;
  color: #EFD2DC;
  transition: color 0.2s;
}

img.mainpicture{
    width: 10em;
    height: 10em;
}

.active {
    font-weight:bolder;
    font-style: italic;
}
p.titlecard{
    background: #b45d7a
    -webkit-linear-gradient(180deg, #b45d7a 0%, #EFD2DC 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-family: 'Titillium Web';
    font-size: 2.1em;
}
div.title{
    display: block;
    margin-bottom: 10px;
    align-self: center;
    padding-left: 1em;
}
ul.header {
    display: block;
    font-family: 'Titillium Web';
    align-self: center;
    padding-left: 0.65em;
    white-space: nowrap;
    color: #EFD2DC;
    font-size: 1.5em;
    transition: color 0.2s;
    list-style-type: none;
    text-decoration: none;
}
ul.header a:hover {
    color: #b45d7a;
}

ul.header a {
    display: inline-block;
    font-family: 'Titillium Web'; 
    color: #d1d1d1;
    transition: color 0.2s;
    list-style-type: none;
    text-decoration: none;
    text-align: left;
    align-self: center;
}

.bm-item:hover {
  color: #d1d1d1;
}

.bm-burger-button {
  position: relative;
  width: 3em;
  height: 3em;
  left: 2em ;
  top: 2em ;
  padding: 0.4em;
  color: #d1d1d1;
  fill: #d1d1d1;
  background-color:none;
  border-radius: 10em;
  transition: background-color 1s;
}
.bm-burger-button:hover {
  background-color: #EFD2DC;
}
img.buttonlogo {
  background-color: #d1d1d1
}
/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #d1d1d1;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #d1d1d1;
}

.bm-cross:hover {
    background: #b45d7a;
}

/* General sidebar styles */
.bm-menu {
  background: #000000;
  padding: 3em 1.5em 0;
  font-size: 1.15em;
  font-family: 'Titillium Web';
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #d1d1d1;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

div.translationbutton {
    position: fixed;
    right: 2em;
    top: 2.5em;
  }

.btn-translationbutton2 {
    background: none;
    color: #EFD2DC;
    margin-left: 1em;
    text-transform: uppercase;
    transition: color 1s, background-color 1s;
}
.btn-translationbutton2:hover {
    background: #EFD2DC;
    color: #b45d7a;
    margin-left: 1em;
    text-transform: uppercase
}
